<template>
  <div class="recommendsongs">
    <div class="top" >
        <video :src="mvurl" controls></video>    
        <!-- <div class="right"></div> -->
    </div>
  <div class="box">
    <img :src="gedan.cover" alt="" class="imgta"> 
    <div class="middle">
        <div class="content">
            <div class="content_1">
                <div>{{gedan.name}}</div>
                <span>{{gedan.artistName}}</span>
            </div>
        </div>
    </div>
    <div class="bottom">
        <div class="bot1">
            {{gedan.desc}}
        </div>
    </div>
  </div>
    <div class="comment">
        <div class="mvcomment">MV评论</div>
        <ul>
            <li v-for="item in comment" :key="item.commentId">
                <img :src="item.user.avatarUrl" alt="">
                <div class="use">{{item.user.nickname}}:</div>
                <div class="com">{{item.content}}</div>
                <span class=" iconfont icon-xihuan"></span>
            </li>
        </ul>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
// import LiItem from '@/components/LiItem'

export default {
  name: 'MvSort',
  data(){
    return{
      gedan:[],
      bigid:0,
      listInfo:null,
      hotList:[],
      allList:[],
      mvurl:null,
      comment:[],
      page:0,//记录页码
      perpage:20//每页显示20条
    }
  },
  props:{
      id:Number,playMusic:Object
  },
  components: {
    // LiItem,
  },
  mounted(){
    window.addEventListener('scroll',this.debounce(this.handleScroll,1000))
  },
  beforeRountLeave(to,from,next){
    window.removeEventListener('scroll',this.debounce(this.handleScroll,1000));
    next();
  },
  methods:{
    debounce(fn, wait) {
      let timeout = null
      return function() {
        if(timeout !== null) clearTimeout(timeout)     
        timeout = setTimeout(fn, wait);
      }
    },
    handleScroll(){
      // console.log(document.documentElement.scrollTop);
      // console.log(document.documentElement.scrollHeight);
      // console.log(document.documentElement.clientHeight);
      let scrollA = document.documentElement.scrollHeight;
      let scrollC = document.documentElement.scrollTop;
      let scrollB = document.documentElement.clientHeight;
      let whnb = scrollA-(scrollC+scrollB);
      // console.log(whnb);
      if(whnb<3){
        // console.log('nb');
          this.page++; // 1  2
          let start=(this.page)*this.perpage;// 20   40
          let end=(this.page+1)*this.perpage;// 40   60
          this.hotList=this.hotList.concat(this.allList.slice(start,end));
          
        
      }
    }
  },
  created(){
    // if(id==){

    // }
  },
  beforeRouteEnter(to,from,next){
    next(vm=>{
            vm.axios.get('/mv/detail?mvid='+vm.id).then(d=>{
                // console.log(d);
                vm.gedan = d.data.data;
                // console.log(vm.gedan);
                vm.commentId = vm.gedan.commentCount;
            });

            vm.axios.get('/mv/url?id='+vm.id).then(f=>{
                // console.log(f);
                vm.mvurl=f.data.data.url;
                // console.log(vm.mvurl);
                
            });

            vm.axios.get('/comment/mv?id='+vm.id).then(d=>{
                // console.log(d);
                vm.comment = d.data.comments;
                // console.log(vm.comment);
            });

        })
  },

  // beforeRouteLeave(){
  //   this.$axios.get('/personalized?limit=100').then(d=>{
  //     console.log(d);
  //     // this.recommend_songs=d.data.result.slice(0,6);
  //   })
  // }
}
</script>

<style lang="less" scoped>
.biao{
  height: 4vh;
  line-height: 4vh;
  padding-left: 2vw;
  width: 100vw;
  font-size: 12px;
  color:white;
  // background-color: #eeeff0;

}
.recommendsongs{
  position: relative;
  // height: 100%;
  margin-top: 80px;
  color: white;
    background: #000000d5;

  .top{
    width: 100vw;
    // height: 28vh;
    // background: #0a2e71;
    position: relative;

    .right{
      width: 48vw;
      position: absolute;
      top: 6vh;
      margin-left: 40vw;

    }
  }
  .middle{
     width: 100%;
     height: 12vh; 
    //  background: #ccc;
     position: relative;
     margin-top: -4px;
     .content{
         height: 14vh;
         width: 80%;
         top: 2vh;
         left: 10%;
         position: absolute;
        //  background: bisque;
         .content_1{
            div{
         width: 100%;

                position: absolute;
                font-size: 20px;
                top: 20%;
                // right: 18%;
                text-align: center;
         }
            span{
         width: 100%;

               position: absolute;
               font-size: 14px;
               top: 50%;
                text-align: center;

              //  right: 20%;
            }
         }

     }
  }
  .bottom{
    width: 100%;
    height: 32vh; 
    // background: #ccc;
    position: relative;
     .bot1{
         text-indent: 2em;
         height: 20vh;
         font-size: 14px;
         width: 80%;
         top: 2vh;
         overflow: hidden;
         left: 10%;
         position: absolute;
         overflow-y:scroll;
     }

  }
}
.box{
  position: relative;
}
.imgta{
  position: absolute;
  top: 0;
  left: 0;
  height: 44vh;
  filter: blur(50px);
}
.comment{
  // height: 40vh;
  width: 100%;
  // background: yellow;
    .mvcomment{
      height: 6vh;
      width: 100%;
      align-items: center;
      text-align: center;
      color: white !important;
      font-size: 24px;
      // margin: 0 auto;
    }
    ul{
      li{
        height: 12vh;
        width: 80%;
        line-height: 10vh;
        // background:skyblue;
        border-bottom: 1px solid rgb(255, 255, 255);
        margin: 0 auto;
        position: relative;

        img{
          height: 40px;
          width: 40px;
          border-radius: 50%;
          background-size: 100%;
          float: left;
          position: absolute;
          top: 25%;
          left: 15px;
        }
        div{
          width: 70%  ;
          margin-left: 70px;
          display: inline-block ;
          text-overflow: ellipsis;
          white-space: nowrap;
          word-break: normal;
          overflow: hidden;

        }
        .com{
          font-size: 12px;
          // height: 8vh;
          top: 18px;
          position: absolute;

        }
        .use{
          // height: 4vh;
          position: absolute;
          font-size: 14px;
        }
        span{
          position: absolute;
          // margin-right: 5px;
          right: 10px;
          font-size: 20px;
          z-index: 999;
          color: rgb(255, 255, 255) ;
        }
      }
  }
}
</style>
